.maincontainer{
    width: 100%;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;   
    position: fixed;
    z-index: 2;
    /* background-color: rgb(0, 0, 0); */
}

.wrapper{
    width: 400px;
    background-color: blue;
    backdrop-filter: blur(10px);
    animation-name: navbar;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    margin-top: 30px;
    overflow: hidden;
    border-radius: 5px;
}

/* .heading{
    width: 70%;
    display: flex;
    font-weight: 900;
    font-size:x-large;
    padding: 30px;
} */

.listitems{
    width: 100%;
    display: flex;
    flex-direction: row;

}
.listitems .item{
    width: calc(100%/3);
    justify-content: center;
    align-items: center;
    text-decoration: none;
    /* background-color: white; */
}

.itemtext{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    padding: 15px 0px 15px 0px;

  
    
}

.temtext.active{
    border-bottom: 3px solid white;
}



/* .itemtext:hover{
    
    
} */

@keyframes navbar {
    0% {background-color: rgba(71, 230, 121, 0.192);}
    50% {background-color: rgba(71, 198, 230, 0.192);}
    100% {background-color: rgba(71, 230, 121, 0.192);}
}